.container {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  height: 100%;
}

.container .welcome {
  color: #69b;
  font-size: 2rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.container form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 400px;
  padding: 1rem;
  background-color: aliceblue;
  border-radius: 10px;
  margin-bottom: 2rem;
}

.container form h2 {
  color: #69b;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.container form input {
  font-size: 1.2rem;
  padding: 0.5rem;
  margin: 1vmin 0;
  border-radius: 2px;
  outline: none;
  border: none;
  background-color: rgb(116, 158, 186);
  color: #4b4b4b;
}

.container form input:focus {
  background-color: rgb(199, 228, 247);
}

.container form input::placeholder {
  color: rgb(199, 228, 247);
}

.container form button {
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  border: none;
  background-color: rgb(29, 161, 29);
  color: #fff;
  margin: 1vmin 0;
}

.container form button:disabled {
  opacity: 0.5;
}

.container form button:hover:enabled {
  cursor: pointer;
  background-color: green;
}

.container .tableContainer {
  width: 100%;
  /* background-color: #4b4b4b; */
  padding: 1rem;
}

.container .tableContainer .controlsContainer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.container .tableContainer .controlsContainer button {
  font-size: 1rem;
  padding: 0.2rem 0.5rem;
  margin-left: 5px;
  border-radius: 2px;
  outline: none;
  border: none;
  background-color: rgb(116, 158, 186);
  color: #eee;
}

.container .tableContainer .controlsContainer button:enabled:hover {
  cursor: pointer;
}
.container .tableContainer .controlsContainer button:enabled:active {
  background-color: rgb(95, 129, 152);
}

.container .tableContainer .controlsContainer button:disabled {
  opacity: 0.5;
}

.container .tableContainer table {
  width: 100%;
}

.container .container .tableContainer table,
th,
td {
  border: 1px solid lightblue;
  border-collapse: collapse;
  padding: 0.5rem;
}

.container .tableContainer table tr td {
  text-align: center;
}

.container .tableContainer table tr td button:last-child {
  width: 40%;
  margin-left: 1vmin;
  padding: 0.3vmin 0;
  background-color: rgb(231, 92, 76);
  color: #eee;
  border: none;
  border-radius: 2px;
}

.container .tableContainer table tr td button:first-child {
  width: 40%;
  padding: 0.3vmin 0;
  background-color: rgb(139, 139, 207);
  color: #eee;
  border: none;
  border-radius: 2px;
}

.container .tableContainer table tr td button:hover {
  cursor: pointer;
}
