.container {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
  /* background-color: rgb(253, 253, 244); */
}

.container .banner {
  width: auto;
  padding: 2vmin;
  color: rgb(116, 184, 245);
  border-bottom: 2px solid grey;
}

.container .banner h3 {
  font-size: 3rem;
}

.container form {
  display: flex;
  flex-direction: column;
  background-color: aliceblue;
  padding: 0.5rem;
  border-radius: 10px;
}

.container form h1 {
  color: #69b;
}

.container form input {
  font-size: 1.5rem;
  padding: 0.5rem;
  margin: 1vmin 0;
  border-radius: 2px;
  outline: none;
  border: none;
  background-color: rgb(116, 158, 186);
  color: #4b4b4b;
}

.container form input:focus {
  background-color: rgb(199, 228, 247);
}

.container form input::placeholder {
  color: rgb(199, 228, 247);
}

.container form small {
  color: rgb(255, 137, 137);
  margin-bottom: 1.1vmin;
}

.container form button {
  font-size: 1.5rem;
  padding: 0.5rem 1rem;
  width: max-content;
  border-radius: 5px;
  border: none;
  background-color: rgb(29, 161, 29);
  color: #fff;
  margin: 1% 0;
}

.container form button:disabled {
  opacity: 0.5;
}

.container form button:hover:enabled {
  cursor: pointer;
  background-color: green;
}

@media (orientation: portrait) {
  .container {
    flex-direction: column;
    justify-content: center;
  }

  .container .banner {
    margin-bottom: 5vmin;
  }
}
